import React from 'react'; 
import ImageGallery from 'react-image-gallery';

import p1 from './Log/Screenshot_20210505-201630.jpg'
import p2 from './Log/Screenshot_20210505-201701.jpg'
import p3 from './Log/Screenshot_20210505-201722.jpg'
import p4 from './Log/Screenshot_20210505-201727__01.jpg'

const Log = () => { 
    return ( 
        <div>
            <h1>Day Tracking App</h1><hr/>

            <div class = "default-text">
                <p>
                This app is a personal project which aims to motive me to do more activities each day.
                </p>

                <p>
                I tried React for a website and now this is my attempt at experiencing it on a mobile platform.
                </p>

                <p>
                The app allows you to create different types of activities. You can set a name and a colour. Once an activity is created you can start adding it to your day. Activities can be also removed from days or deleted from the list of activities created.
                </p>

                <p>
                All the data is saved on the phone in a database.
                </p>

                <p>
                On launch, the app will always highlight the current day.
                </p>

                <h4>
                    Source code available on request.
                    Work in progress.
                </h4>

                <div class = "gallery">
                <MyGallery />
                </div>

            </div>
        </div>
    )
}

const images = [
    {
        original: p1,
        thumbnail: p1,
    },
    {
        original: p2,
        thumbnail: p2,
    },
    {
        original: p3,
        thumbnail: p3,
    },
    {
        original: p4,
        thumbnail: p4,
    }
  ];

class MyGallery extends React.Component {
    render() {
      return <ImageGallery style={{width: "100px", height: "200px"}} items={images} />;
    }
  }

export default Log; 