import React from 'react'; 
import ImageGallery from 'react-image-gallery';
import { Component } from 'react';
import ReactPlayer from 'react-player';

import p2 from './Atestat/received_1339602966109395.png'
import p3 from './Atestat/amira_1.png'
import p4 from './Atestat/IMG-20160217-WA0019.jpeg'
import p5 from './Atestat/received_1901700126547632.png'
import p12 from './Atestat/12931263_1750749211803372_3180139505013316319_n.jpg'
import p13 from './Atestat/info.PNG'


const Subzero = () => { 
    return ( 
        <div>
            <h1>Subzero</h1><hr/>

            <div class ="player-wrapper">
                <MyVideo/>
            </div>

            <div class = "default-text">
                <p>
                My high school required every student studying computer science to create a project before they graduate. My teacher wanted us to create something using either C# or C++. I saw an opportunity to create something in Unity. 
                </p>

                <p >
                The player only has to pay attention to the symbols it sees throughout the level. At the end of the level the player needs to interact with the right tree in the correct order. The game plays a musical note if you activated the correct symbol. Otherwise, the character will inform you about the wrong combination.
                </p>

                <p>
                This project was my first experience with animations and motion capture. I also did more research on lighting to understand how to build a night scene. Moreover, I became aware of hardware constraints when I started adding foliage in the world.
                </p>

                <p>
                This project won second place at the Infomatrix international competition.
                </p>   

            <div>
              <a href="https://drive.google.com/open?id=1RLSQv_5p282hf1_H2yRL2C5jxYdY7bYM" target="_blank"><button>Click here to see the project</button></a>
            </div>

            </div>
            <div class = "gallery">
                <MyGallery />
            </div>
        </div>
    )
}

const images = [
    {
        original: p2,
        thumbnail: p2,
    },
    {
        original: p3,
         thumbnail: p3,
    },
    {
        original: p4,
        thumbnail: p4,
    },
    {
        original: p5,
        thumbnail: p5,
    },
    {
        original: p12,
        thumbnail: p12,
    },
    {
        original: p13,
        thumbnail: p13,
    },
  ];

class MyGallery extends React.Component {
    render() {
      return <ImageGallery style={{width: "100px", height: "200px"}} items={images} />;
    }
  }

  class MyVideo extends Component {
    render () {
      return <ReactPlayer url='https://www.youtube.com/watch?v=axWS_G8idt4' playing muted loop controls='true' width = '1280px' height = '720px'/>
    }
  }

export default Subzero; 