import React from 'react'; 


const Discord = () => { 
    return ( 
        <div>
            <h1>Discord bot</h1><hr/>

            <div class = "default-text">
                <p>
                My friend recently started streaming and became famous super fast. So he decided to create a community on discord. This community required tedious moderation such as, assigning a role to new members or deleting messages posted in the wrong channel. 
                </p>

                <p >
                The server owner also wanted to have a music bot, a stream bot, a time out bot, black/white list bot. However, non-nitro servers are limited to only a small number of bots.
                </p>

                <p>
                My bot aims to solve all of these problems. I am collaborating closely with the streamer and the moderation team to provide the best experience for them and the community.
                </p> 

                <p>
                Currently, the bot can:
                    <ul>
                        <li>
                        Detect roles based on a hierarchy
                        </li>
                        <li>
                        Play/Queue/Shuffle/Pause/Loop/Skip music
                        </li>
                        <li>
                        Assign a default role on join
                        </li>
                        <li>
                        Detect spam, mute, warn and ban users
                        </li>
                        <li>
                        Blacklist certain words and delete them on detection
                        </li>
                        <li>
                        Display greeting message
                        </li>
                        <li>
                        Display a help banner
                        </li>
                        <li>
                        Moderate content per channel
                        </li>
                        <li>
                        A lot of helper functions for the moderation team
                            <ul>
                                <li>
                                Add/Remove/View greeting messages
                                </li>
                                <li>
                                Add/Remove/View blacklisted words
                                </li>
                            </ul>
                        </li>
                       
                    </ul> 
                </p>

                <h4>
                    Source code and server available on request.
                </h4>

            </div>
        </div>
    )
}


export default Discord; 