import React from 'react'; 
import { Component } from 'react';
import ReactPlayer from 'react-player';

const BadFood = () => { 
    return ( 
        <div>
            <h1>2D Game</h1><hr/>

            <div class ="player-wrapper">
                <MyVideo/>
            </div>

            <div class = "default-text">
                <p>
                This project was part of a university gaming module. The module was aiming to help us understand 3D simulation techniques.
                </p>

                <p >
                It focused on:
                <ul>
                    <li>
                    Newtonian dynamics - all entities in the game move via the application of Newtonian dynamics. (manipulating position, associated derivatives, mass).
                    </li>
                    <li>
                    Collision detection between spheres and AABBs
                    </li>
                    <li>
                    Collision response - collecting items, bouncing off walls, detecting hits
                    </li>
                    <li>
                    AI - FSM, flocking
                    </li>
                    <li>
                    Pathfinding - A*
                    </li>
                </ul>
                </p>

                <h2>Game overview</h2>
                <h3>Tiles</h3>
                <p>
                The game map is made out of tiles. Floor tiles can be walkable or non-walkable (walls). Grass will slow down the player by 30% of their speed.
                </p> 

                <h3>Player</h3>
                <p>
                The player controls the character with the arrow keys. The player starts the game with 3 lives. If the player moves over a food item, it is collected and the number on the screen raises by 1. The player can hold a maximum of 5 items at once. If the player touches the food shop, the found counter is reset to 0 and the player gains points based on the Fibonacci sequence. If the player is hit by an enemy or projectile they lose all the food collected. They will also lose a life point and start over from the spawning point. On 0 lives the game resets and starts from the beginning.
                </p> 

                <h3>Enemies</h3>
                <p>
                Every 15 seconds, an enemy will spawn in the walled-off area up to a maximum of 20 robots.
                </p> 
                <p>
                They have 3 modes:
                <ul>
                    <li>
                    Patrol mode: If the player is 80 units away, the robot randomly picks a walkable tile to go to.
                    </li>
                    <li>
                    Attack mode: if the player is within 80 units, the robot will try to reach the player and fire a laser at the player every 2 seconds.
                    </li>
                    <li>
                    Protect mode: If the player is holding 5 food items, the robot should select a random point within 120 units of the food shop.
                    </li>
                </ul>
                </p> 
                <p>
                Enemies use a simple FSM to change between states and use A* to navigate the tiles. Grass tiles have a higher cost than other tiles.
                </p> 
                <p>
                To prevent collision between robots, they implement a flocking behaviour.
                </p> 

                <h3>Laser</h3>
                <p>
                Enemies can fire lasers. They will bounce off walls or buildings 4 times before they get destroyed. If a player touches a laser it loses 1 health point. If another robot is touched by a laser it freezes for 3 seconds.
                </p> 

                <h3>Food</h3>
                <p>
                Spawns every 5 seconds and despawns after 12 seconds. Can be exchanged for points at the food shop.
                </p> 

                <h3>Coins</h3>
                <p>
                Spawns every 15 seconds. It can be picked up just like the food. The player can exchange coins for balloons at the balloon shop.
                </p> 

                <h3>Balloon</h3>
                <p>
                If the player has a balloon in their inventory, they can press space to deploy it. The balloon makes the player invincible to attacks for 5 seconds. The balloon uses springs to bounce around the player while being dragged.
                </p> 

                <h4>
                Source code available on request.
                </h4>
            </div>
        </div>
    )
}

  class MyVideo extends Component {
    render () {
      return <ReactPlayer url='https://youtu.be/IYa8S84e0nU'playing muted loop controls='true' width = '1280px' height = '720px'/>
    }
  }

export default BadFood; 