import React from 'react'; 
import { Component } from 'react';
import ReactPlayer from 'react-player';


const Xmas = () => { 
    return ( 
        <div>
            <h1>X-Mas</h1><hr/>

            <div class ="player-wrapper">
                <MyVideo/>
            </div>

            <div class = "default-text">
                <p>
                This was the second game I created. My teacher decided for that year’s Christmas we would do Secret Santa with a twist. We had to develop a Christmas themed application and make sure the person seeing us presenting it would know it was for them.
                </p>

                <p >
                I decided to create an interactive book, pick your own adventure style. Player would read then decide what happens next. On some of the pages I wanted to have 3D models “unfolding” to make the book interesting and dynamic but I was not able to do that due to my limited knowledge back then. That is why on one of the pages you can see a cube sticking out.
                </p>

                <p>
                Some of my friends helped writing the story and came up with loads of inside jokes.
                </p> 

            <div>
              <a href="https://drive.google.com/open?id=1pWxRJjmCsYng0nzMeaU4_tBJk9hzlLPS" target="_blank"><button>Click here to see the project</button></a>
            </div>

            </div>
        </div>
    )
}

  class MyVideo extends Component {
    render () {
      return <ReactPlayer url='https://youtu.be/vrENl45PddA' playing muted loop controls='true' width = '1280px' height = '720px'/>
    }
  }

export default Xmas; 