import React from 'react'; 
import { Component } from 'react';
import ReactPlayer from 'react-player';

const TRAOD = () => { 
    return ( 
        <div>
            <h1>Tomb Raider: Angel of Darkness Remake</h1><hr/>

            <div class ="player-wrapper">
                <MyVideo/>
            </div>

            <div class = "default-text">
                <p>
                As part of my gaming degree, we were tasked with creating a full video game. I decided to recreate one of my favourite games, Tomb Raider: Angel of Darkness. The game had issues with the controls so part of the challenge was updating the outdated controls and camera.
                </p>

                <p >
                The level I recreated is "Hall of Seasons". I picked this level because it offers a lot of gameplay opportunities and puzzles (and a potential boss fight).
                </p>

                <h2>Game overview</h2>
                <h3>Camera</h3>
                <p>
                The game is using a modern 3D person over the shoulder camera (commonly seen in action games). The user can freely swap the shoulder of the camera.
                </p> 

                <h3>Animation</h3>
                <p>
                I used animation blending for locomotion. Climbing and jumping use mantling. I also implemented a ragdoll system for when the health bar reaches 0.
                </p> 

                <h3>Weapons</h3>
                <p>
                There are different weapons the player can use. When aiming the camera moves closer to the player. Currently, it doesn't fire any projectiles.
                </p> 

                <h3>Damage</h3>
                <p>
                The player has a health bar. Currently, it only goes down from fall damage or from environment threats. On death, the player ragdolls and it is prompted with a game over screen. From there, they can pick to reload from the last checkpoint or a save file.

                </p> 

                <h3>Checkpoint</h3>
                <p>
                The checkpoint system is using volume boxes to detect if a player entered an area. If they did, it writes to a save file on the hard drive the location, rotation of the player plus other information such as the inventory and the level status. Using the save game button functions the same.
                </p> 

                <h3>Level loading</h3>
                <p>
                For performance reasons, I had to look into loading/unloading different part of the level as you progressed through the puzzles.
                </p> 

                <h3>Audio</h3>
                <p>
                Each room has dynamic 3D audio that activates from certain triggers.
                </p> 

                <h3>Effects</h3>
                <p>
                I learned how to use the Niagra system to create particle effects, such as fire and sparks.
                </p> 

                <h3>Shaders</h3>
                <p>
                The tree in the main room uses a shader to display different types of leaves. 4 invisible pillars in the room are used to calculate an angle. This angle is used to determine what the player can or can't see (Hellblade does the same mechanic during the Valravn section).
                </p> 

                <h4>
                Source code available on request. Work in progress.
                </h4>
            </div>
        </div>
    )
}

  class MyVideo extends Component {
    render () {
      return <ReactPlayer url='https://youtu.be/ev8XFzqAjqM' playing muted loop controls='true' width = '1280px' height = '720px'/>
    }
  }

export default TRAOD; 