import React from 'react'; 
import ImageGallery from 'react-image-gallery';
import { Component } from 'react';
import ReactPlayer from 'react-player';

import p5 from './UniProjects/unknown-27.png'

const Graphics = () => { 
    return ( 
        <div>
            <h1>Graphics for Games</h1><hr/>
            <div class = "default-text">
                <p>
                This module taught me how a graphics pipeline works. I had to familiarise myself with OpenGl syntax and GLSL.
                </p>

                <p>
                For the first project I had to create a scene with movable 2D and 3D objects. The scene was filled with stars to show perspective. As an extra feature, I coded a first person controller so you can rotate the camera and move around.
                </p>

                <p>
                For the second project I had to create advanced shaders.  I was given a rotating cube and I had to “destroy” it in creative ways. My first approach was to make it split into multiple cubes through a geometry shader. My second approach was to tessellate the surface of the cube and make it look like it shattered into a loads of colourful shards. My final advanced shader was adding a skybox to the scene and making the cube reflect and refract it.
                </p>

                <h4>
                    Source code available on request.
                </h4>


            </div>
            <div class ="player-wrapper">
                <MyVideo/>
            </div>
            <div class = "gallery">
                <MyGallery />
            </div>
        </div>
    )
}

const images = [
    {
        original: p5,
        thumbnail: p5,
    },
  ];

class MyGallery extends React.Component {
    render() {
      return <ImageGallery style={{width: "100px", height: "200px"}} items={images} />;
    }
  }

class MyVideo extends Component {
    render () {
      return <ReactPlayer url='https://youtu.be/rGoY0Vqqsqs' playing muted loop controls='true' width = '1280px' height = '720px'/>
    }
  }

export default Graphics; 