import React from 'react'; 
import { Component } from 'react';
import ReactPlayer from 'react-player';


const Chess = () => { 
    return ( 
        <div>
            <h1>Simple Chess Game</h1><hr/>

            <div class ="player-wrapper">
                <MyVideo/>
            </div>

            <div class = "default-text">
                <p>
                This coursework was focused on the collision between chess pieces. I had to simulate a chess game with 3 types of pieces, queen, bishop and rook. The pieces would move randomly by a small amount and pieces of the same type would be in the same team. The game simulation would give information on collisions and at the end of the game would print the score.
                </p>

                <p>
                I had to create my own functions (if I remember correctly, we had some restrictions on the libraries we could use) and overriding operators while ensuring no memory leaks.
                </p>

                <h4>
                    Source code available on request.
                </h4>

            </div>
        </div>
    )
}


class MyVideo extends Component {
    render () {
      return <ReactPlayer url='https://youtu.be/nJjvdCtz2kI' playing muted loop controls='true' width = '1280px' height = '720px'/>
    }
  }

export default Chess; 