import React from 'react'; 
import ImageGallery from 'react-image-gallery';

import p1 from './Lol/1.jpg'
import p2 from './Lol/2.jpg'
import p3 from './Lol/3.jpg'
import p4 from './Lol/team.PNG'

const Lol = () => { 
    return ( 
        <div>
            <h1>League of Legends</h1><hr/>
            <div class = "default-text">
                <p>
                I have been playing this game for about 7 years. Recently, I became a semi-pro player, playing for a Romanian club called Nexus Gaming. I am also part of a female only team (part of the same organisation) which qualified for the top bracket of WEL.
                </p>
                <p>
                This game helped me improve many skills. I learned how to communicate fast and concise information about the status of the game which ultimately led my teams to victory. League of Legends is a fast paced game which in the end will make you make decisions under a lot of pressure and do critical thinking.
                </p>
                <p>
                Playing this game has also opened my eye to what reflection means. To improve my gameplay, I would watch my past games and figure out what I did wrong and what can I do in the future to avoid certain outcomes or improve the situation. 
                </p>

                <p>
                <a href= "http://nexusesports.ro/" target="_blank"> You can visit their website here. </a>
                
                </p>

                <div class = "gallery">
                <MyGallery />
                </div>

            </div>
            
        </div>
    )
}

const images = [
    {
        original: p1,
        thumbnail: p1,
    },
    {
        original: p2,
        thumbnail: p2,
    },
    {
        original: p3,
        thumbnail: p3,
    },
    {
        original: p4,
        thumbnail: p4,
    }
  ];

class MyGallery extends React.Component {
    render() {
      return <ImageGallery style={{width: "100px", height: "200px"}} items={images} />;
    }
  }

export default Lol; 