import React, { useLayoutEffect } from 'react';
import './App.css';
import { bubble as Menu } from 'react-burger-menu'
import {
  HashRouter as Router,
  Switch,
  Route,
  Link,
  useLocation
} from "react-router-dom";
import { HashLink } from 'react-router-hash-link';

import Particles from "react-particles-js";
import Home from './Home.js';
import GameCarousel from './GameCarousel';
import Games from './Games';
import Memories from './Projects/memories';
import Xmas from './Projects/xmas';
import Subzero from './Projects/subzero';
import Saggezza from './Projects/saggezza';
import RTS from './Projects/rts';
import Uni from './Projects/uniProjects';
import Hobby from './Hobbies';
import Drawings from './Personal/drawings';
import Aikido from './Personal/aikido';
import Ncl from './Personal/ncl';
import Lol from './Personal/lol';
import Films from './Personal/films';
import Management from './Personal/management';
import Hackathons from './Personal/hackathons';
import Graphics from './Projects/graphics';
import Chess from './Projects/chess';
import Log from './Projects/log';

import "react-image-gallery/styles/css/image-gallery.css"
import Discord from './Projects/discord';
import BadFood from './Projects/badfood';
import TRAOD from './Projects/traod';

function AppCore() {
  const location = useLocation();
  // Scroll to top if path changes
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname]);

  return (
    <div className="App">   

  <Menu>
    <HashLink to='/#intro' style={{ textDecoration: 'none', color: "white" }} id="home" className="menu-item"><h1 class ="menuH">Intro</h1></HashLink>
    <HashLink to='/#about' style={{ textDecoration: 'none', color: "white" }} id="home" className="menu-item"><h1 class ="menuH">About me</h1></HashLink>
    <HashLink to="/#projects" style={{ textDecoration: 'none', color: "white" }} id="games" className="menu-item" ><h1 class ="menuH">My Projects</h1></HashLink>
    <HashLink to="/#hobby" style={{ textDecoration: 'none', color: "white" }} id="hobbies" className="menu-item"><h1 class ="menuH">My Hobbies</h1></HashLink>
  </Menu>

  <div className="particles">
            <Particles
            params={{
                particles: {
                number: {
                    value: 60,
                    density: {
                    enable: true,
                    value_area: 500,
                    },
                },
                line_linked: {
                    enable: true,
                    opacity: 0.02,
                },
                move: {
                    direction: "right",
                    speed: 0.1,
                },
                size: {
                    value: 1,
                },
                opacity: {
                    anim: {
                    enable: true,
                    speed: 1,
                    opacity_min: 0.05,
                    },
                },
                },
                interactivity: {
                events: {
                    onclick: {
                    enable: true,
                    mode: "push",
                    },
                },
                modes: {
                    push: {
                    particles_nb: 1,
                    },
                },
                },
                retina_detect: true,
            }}
            />
        </div>

  <Switch>
          <Route path="/games">
            <GameCarousel games={Games} title={"My Game Projects"}/> 
          </Route>
          <Route path="/memories"> 
            <Memories />
          </Route>
          <Route path="/xmas"> 
            <Xmas />
          </Route>
          <Route path="/discord">
            <Discord/>
          </Route>
          
          <Route path="/subzero"> 
            <Subzero />
          </Route>
          <Route path="/saggezza"> 
            <Saggezza />
          </Route>
          <Route path ="/badfood">
            <BadFood/>
          </Route>
          <Route path ="/graphics">
          <Graphics/>
          </Route>
          <Route path ="/chess">
            <Chess/>
          </Route>
          <Route path="/rts"> 
            <RTS />
          </Route>
          <Route path="/log">
            <Log/>
          </Route>
          <Route path="/TRAOD">
            <TRAOD/>
          </Route>
          <Route path="/uniProjects"> 
            <Uni />
          </Route>
          <Route path="/hobbies"> 
            <GameCarousel games={Hobby} title={"My Hobbies"}/> 
          </Route>
          <Route path="/drawings"> 
            <Drawings />
          </Route>
          <Route path="/aikido"> 
            <Aikido />
          </Route>
          <Route path="/ncl"> 
            <Ncl />
          </Route>
          <Route path="/lol"> 
            <Lol />
          </Route>
          <Route path="/films"> 
            <Films />
          </Route>
          <Route path="/management"> 
            <Management />
          </Route>
          <Route path="/hackathons"> 
            <Hackathons />
          </Route>
          <Route path="/">
           <Home />
          </Route>
        </Switch>
    </div>
  );
}

const App = () => ( 

  <Router basename="/"> 
    <AppCore /> 
  </Router>
)

export default App;
